import ReactTooltip from 'react-tooltip'
import abilitiesJSON from '../abilities.json'
const Abilities = (props) => {
  const { Name, Ability, Ability2, HiddenAbility } = { ...props }

  function formatAbilityDescription(ability, JSON) {
    let description = JSON[ability]
    let descriptionTokens = description.split(' ')
    let sentenceLength = 0
    let formattedDescription = ''
    descriptionTokens.forEach((token) => {
      sentenceLength += token.length
      formattedDescription += token + ' '
      if (sentenceLength >= 35) {
        sentenceLength = 0
        formattedDescription += '<br/>'
      }
    })
    return formattedDescription
  }

  function getIdAndTooltip(ability) {
    let id, tooltip
    if (ability !== '') {
      id = `${Name}-${ability}`
      tooltip = formatAbilityDescription(ability, abilitiesJSON)
    } else {
      id = ''
      tooltip = ''
    }
    return [id, tooltip]
  }

  let id1, id2, id3, tooltip1, tooltip2, tooltip3
  ;[id1, tooltip1] = getIdAndTooltip(Ability)
  ;[id2, tooltip2] = getIdAndTooltip(Ability2)
  ;[id3, tooltip3] = getIdAndTooltip(HiddenAbility)

  function reactTooltip(id) {
    return (
      <ReactTooltip
        id={id}
        place='bottom'
        multiline={true}
        effect='solid'
      ></ReactTooltip>
    )
  }

  const styles = { display: 'table-cell', margin: '1rem' }

  return (
    <div
      style={{
        margin: 'auto',
        textAlign: 'center',
        padding: '1.5rem',
      }}
    >
      <div style={styles}>
        <p className={`no-margin`}>Abilities</p>
        <div>
          <p data-for={id1} data-tip={tooltip1} className={`no-margin`}>
            {Ability}
          </p>
          {Ability2 !== '' ? (
            <p data-for={id2} data-tip={tooltip2} className={`no-margin`}>
              {Ability2}
            </p>
          ) : null}
        </div>
        <p className={`no-margin`}>Hidden Ability</p>
        {HiddenAbility !== '' ? (
          <p data-for={id3} data-tip={tooltip3} className={`no-margin`}>
            {HiddenAbility}
          </p>
        ) : (
          <p className={`no-margin`}>None</p>
        )}
        {reactTooltip(id2)}
        {reactTooltip(id1)}
        {reactTooltip(id3)}
      </div>
    </div>
  )
}

export default Abilities
