import React from 'react'
import ReactDOM from 'react-dom'
import reportWebVitals from './reportWebVitals'
import './index.css'
import Header from './components/Header'
import Team from './components/Team'
import Filters from './components/Filters'
import IconContainer from './components/IconContainer'
class App extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      region: 'all',
      gen: 'all',
      type: 'all',
      type2: 'all',
      ability: 'all',
      stat: 'None',
      name: '',
      pokemon: [],
      team: [],
    }
  }

  componentDidMount() {
    const pokemon = require('./nationaldex.json')
    this.setState({ pokemon: pokemon })
  }

  render() {
    const pokemon = this.state.pokemon

    // filter variables
    const ability = this.state.ability
    const gen = this.state.gen
    const name = this.state.name
    const region = this.state.region
    const stat = this.state.stat
    const type = this.state.type
    const type2 = this.state.type2
    const team = this.state.team

    // filter by name
    let filterPokemon = pokemon.filter((pokemon) => {
      if (name === '') return pokemon
      return pokemon.Name.toLowerCase().includes(name.toLowerCase())
    })
    // by generation
    filterPokemon = filterPokemon.filter((pokemon) => {
      if (gen === 'all') return pokemon
      else return pokemon.Gen === parseInt(gen)
    })
    // by primary type
    filterPokemon = filterPokemon.filter((pokemon) => {
      if (type === 'all') return pokemon
      else return pokemon.Type === type
    })
    // by secondary type
    filterPokemon = filterPokemon.filter((pokemon) => {
      if (type2 === 'all') return pokemon
      else return pokemon.Type2 === type2
    })
    // by ability
    filterPokemon = filterPokemon.filter((pokemon) => {
      if (ability === 'all') return pokemon
      else
        return (
          pokemon.Ability === ability ||
          pokemon.Ability2 === ability ||
          pokemon.HiddenAbility === ability
        )
    })
    // by stat in descending order
    if (stat !== 'None') {
      filterPokemon.sort(function (a, b) {
        return b[stat] - a[stat]
      })
    }

    const handleFilter = (event) => {
      const target = event.target
      const name = target.name
      const value = target.value
      this.setState({
        [name]: value,
      })
    }

    const addTeamMember = (pokemon) => {
      let updatedTeam = this.state.team
      updatedTeam.push(pokemon)
      this.setState({ team: updatedTeam })
    }

    const removeTeamMember = (pokemon) => {
      let updatedTeam = this.state.team
      const index = updatedTeam.indexOf(pokemon)
      if (index > -1) {
        updatedTeam.splice(index, 1)
      }
      this.setState({ team: updatedTeam })
    }

    return (
      <div className='container'>
        <div className='container-item'>
          <Header header={`Pokémon Team Builder`} />
          <div className='team-container'>
            <Team
              team={team}
              pokemon={pokemon}
              removeTeamMember={removeTeamMember}
            />
          </div>
        </div>
        <div className='container-item'>
          <Filters
            ability={ability}
            gen={gen}
            name={name}
            region={region}
            stat={stat}
            type={type}
            type2={type2}
            handleFilter={handleFilter}
          />
          <IconContainer
            team={team}
            filterPokemon={filterPokemon}
            addTeamMember={addTeamMember}
          />
        </div>
      </div>
    )
  }
}

ReactDOM.render(<App></App>, document.getElementById('root'))

reportWebVitals()
