import './Icon.css'
import './Types.css'
import { missingPokemonId, fillColour } from '../variables'
const Icon = (props) => {
  const { Name, No, Type, Type2 } = { ...props }

  let secondaryType

  Type2 !== '' ? (secondaryType = Type2) : (secondaryType = Type)

  return (
    <div
      className={`${Type}-light ${secondaryType}-border icon`}
      style={{ color: `${fillColour[Type]}` }}
    >
      {missingPokemonId.includes(No) ? '-' : ''}
      <img src={`${process.env.PUBLIC_URL}/icons/${No}.png`} alt={Name}></img>
    </div>
  )
}

export default Icon
