const Filter = (props) => {
  const { filterName, filterValue, filterData, handleFilter, type } = {
    ...props,
  }

  function capitalizeFirstLetter(string) {
    return string.charAt(0).toUpperCase() + string.slice(1)
  }

  const optionStyle = { backgroundColor: '#black' }

  const style = { maxWidth: `134px`, borderColor: `#afafafafaf` }

  return (
    <div className={'filter-item'}>
      <p>{capitalizeFirstLetter(filterName)}</p>
      {type === 'select' ? (
        <select
          className={`filter-option`}
          name={filterName}
          value={filterValue}
          onChange={handleFilter}
          style={style}
        >
          {filterData.map((data) => {
            return (
              <option key={data} value={data} style={optionStyle}>
                {data}
              </option>
            )
          })}
        </select>
      ) : (
        <input
          className={`filter-option`}
          name={filterName}
          value={filterValue}
          onChange={handleFilter}
          style={style}
        ></input>
      )}
    </div>
  )
}

export default Filter
