import Icon from './Icon'
import './IconContainer.css'
const IconContainer = (props) => {
  const { addTeamMember, filterPokemon, team } = { ...props }
  const styles = {
    
  }
  const pokemon = filterPokemon.map((pokemon) => {
    return (
      <li
        key={pokemon.No}
        onClick={() => {
          if (
            (team.length === 0 || team.length < 6) &&
            !team.includes(pokemon)
          ) {
            addTeamMember(pokemon)
          }
        }}
      >
        <div>
          <div>
            <Icon {...pokemon} />
          </div>
          <div>
            <p>{pokemon.Name}</p>
          </div>
        </div>
      </li>
    )
  })
  return (
    <div className={'icon-container'} style={styles}>
      <ul>{pokemon}</ul>
    </div>
  )
}

export default IconContainer
