import './Types.css'
function Types(props) {
  const { Type, Type2 } = { ...props }

  const secondaryType =
    Type2 === '' ? (
      <div
        className={`type ${Type} ${Type}-border round-border-white-text Type`}
        style={{ visibility: 'hidden' }}
      >
        {Type}
      </div>
    ) : (
      <div
        className={`type ${Type2} ${Type2}-border round-border-white-text Type`}
      >
        {Type2}
      </div>
    )

  return (
    <div className={`Type-container`}>
      <div className={`${Type} ${Type}-border round-border-white-text Type`}>
        {Type}
      </div>
      {secondaryType}
    </div>
  )
}

export default Types
