import Image from './Image'
import Types from './Types'
import Abilities from './Abilities'
import BarChart from './Stats'
// import TypeEffectiveness from './TypeEffectiveness'
import MoveSet from './MoveSet'
import './Team.css'
const Team = (props) => {
  const { removeTeamMember, team } = { ...props }

  const pokemonTeam = team.map((pokemon) => {
    return (
      <div key={pokemon.No} className={`pokemon-container`}>
        <div
          onClick={() => {
            removeTeamMember(pokemon)
          }}
        >
          <Image {...pokemon} removeTeamMember={removeTeamMember} />
          <p>{pokemon.Name}</p>
          <Types {...pokemon} />
        </div>
        <Abilities {...pokemon} />
        <BarChart {...pokemon} />
        <MoveSet {...pokemon} />
        {/* <TypeEffectiveness {...pokemon} /> */}
      </div>
    )
  })
  return <div>{pokemonTeam}</div>
}

export default Team
