import Filter from './Filter'
import './Filters.css'
import { regions, gens, types, abilities, stats } from '../filters.js'
const Filters = (props) => {
  const { ability, gen, name, region, stat, type, type2, handleFilter } = {
    ...props,
  }

  const selectFilterData = [
    { name: 'region', value: region, data: regions, type: 'select' },
    { name: 'gen', value: gen, data: gens, type: 'select' },
    { name: 'type', value: type, data: types, type: 'select' },
    { name: 'type2', value: type2, data: types, type: 'select' },
    { name: 'ability', value: ability, data: abilities, type: 'select' },
    { name: 'stat', value: stat, data: stats, type: 'select' },
    { name: 'name', value: name, type: 'input' },
  ]

  const selectFilters = selectFilterData.map((filter) => {
    return (
      <Filter
        key={filter.name}
        filterName={filter.name}
        filterValue={filter.value}
        filterData={filter.data}
        type={filter.type}
        handleFilter={handleFilter}
      />
    )
  })

  return (
    <div className='box-shadow margin-bottom'>
      <h1 style={{ margin: `2rem 1rem 1rem 3rem` }}>Filters</h1>
      <div className={'filter-container'}>{selectFilters}</div>
    </div>
  )
}

export default Filters
