import './Types.css'
const Image = (props) => {
  const { No, Name, Type, Type2 } = { ...props }

  const styles = {
    borderRadius: '50%',
    borderWidth: '16px',
    maxHeight: '150px',
    maxWidth: '150px',
    width: '100%',
    cursor: 'pointer',
  }

  let secondaryType

  Type2 !== '' ? (secondaryType = Type2) : (secondaryType = Type)

  return (
    <div
      className={`image ${Type}-light ${secondaryType}-border`}
      style={styles}
    >
      <img src={`${process.env.PUBLIC_URL}/images/${No}.png`} alt={Name}></img>
    </div>
  )
}

export default Image
