import ReactTooltip from 'react-tooltip'
import './MoveSet.css'
const movesDescriptions = require('../move-description.json')
const MoveSet = (props) => {
  const { Name, MoveSet } = { ...props }

  function formatMoveDescription(description) {
    let descriptionTokens = description.split(' ')
    let sentenceLength = 0
    let formattedDescription = ''
    descriptionTokens.forEach((token) => {
      sentenceLength += token.length
      formattedDescription += token + ' '
      if (sentenceLength >= 35) {
        sentenceLength = 0
        formattedDescription += '<br/>'
      }
    })
    return formattedDescription
  }

  const nameAndLevel = (level, name) => {
    const style = {
      minWidth: `150px`,
      maxWidth: `150px`,
      marginBottom: `3px`,
      fontWeight: 'bold',
    }
    return (
      <div className={` margin-auto`} style={style}>
        {`Lvl.${level} ${name} `}
      </div>
    )
  }

  const powerAndAccuracy = (power, accuracy) => {
    const style = {
      minWidth: `50px`,
      maxWidth: `50px`,
      textAlign: 'end',
      marginRight: `5px`,
      fontWeight: 'bold',
    }
    return (
      <div className={`margin-auto`} style={style}>
        {`${power} ${accuracy}`}
      </div>
    )
  }

  const categoryImg = (category) => {
    return (
      <div className={`margin-auto`}>
        <div className={`category-img-container`}>
          <img
            className={`move-category-img`}
            src={`${process.env.PUBLIC_URL}/${category}.png`}
            alt={`${category}`}
          ></img>
        </div>
      </div>
    )
  }

  return (
    <div>
      <p>Lvl Up Move Set</p>
      <div className='move-set-container'>
        {MoveSet.map((move) => {
          // move data
          let [level, name, type, category, power, accuracy] = [...move]
          type = type.toLowerCase()
          let id = `${Name}-${name}`
          let description = formatMoveDescription(movesDescriptions[`${name}`])
          let tooltip = `${description}`
          return (
            <div
              key={`${level}-${Name}-${name}`}
              data-for={id}
              data-tip={tooltip}
              className={`move-container flex ${type} ${type}-border round-border-white-text`}
            >
              {nameAndLevel(level, name)}
              <div className={`flex margin-auto `}>
                {powerAndAccuracy(power, accuracy)}
                {categoryImg(category)}
              </div>
              <ReactTooltip
                id={id}
                place='bottom'
                multiline={true}
                effect='solid'
                style={{ fontWeight: 'normal' }}
              ></ReactTooltip>
            </div>
          )
        })}
      </div>
    </div>
  )
}

export default MoveSet
